.hero-container{
    width: 100%;
    height: auto;
  
}

#video{
    width: 100%;
    height: 100%;
}

.hero-text{
    position: absolute;
    top: 25%;
    left: 20%;
    right: 20%;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 2rem;
}
@media(max-width:990px){
    .hero-text{
        position: absolute;
        top: 18%;
        
    }
}
@media(max-width:475px){
    .hero-text{
        position: absolute;
        top: 15%;
        
    }
}


.hero-text p{
    color: white;
    font-size: 3vw;
    text-align: center;
    font-weight: 500;
    display: flex;
    flex-wrap: wrap;
}
.shop-btn{
   position: absolute;

   top: 41%;
   left: 43%;
   right: 43%;
   margin-top: 2rem;

   
}
.btn:hover{
   background-color: black;
   transition: 0.3s ease-in-out;
}
.btn{

    padding: 13px 55px ;
    font-size: 12px;

    padding: 1rem 2rem ;
    font-size: 0.7rem;

    background-color: hsla(0, 84%, 48%, 0.834);
    color: white;
    border: none;
 
}
@media (max-width:990px){
    .shop-btn{
        position: absolute;
        top: 26%;
        left: 42%;
        right: 21%;
        margin-top: 1rem ;
     }

}
@media (max-width:475px){
    .shop-btn{
        position: absolute;
        top: 19%;
        left: 30%;
        right: 30%;
        margin-top: 1rem;

    
     }

}