.product-listing {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
  }
  
  .product {
    border: 1px solid #ccc;
    padding: 10px;
  }
  
  .product img {
    width: 100%;
    height: auto;
  }
  
  @media screen and (max-width: 768px) {
    .product-listing {
      grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    }
  }
  